import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'

Vue.use(VueRouter)

const routes = [{
        path: '/',
        name: 'Home',
        component: Home
    },
    {
        path: "/city/:cityName",
        name: "City",
        component: () =>
            import ("../views/City.vue")
    },
    {
        path: "/covidcity/:citys",
        name: "CovidCity",
        component: () =>
            import ("../views/CovidCity.vue"),
        props: true
    }
]

const router = new VueRouter({
    mode: 'history',
    base: process.env.BASE_URL,
    routes
})

export default router